body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
    font-family: 'ExpresswayRg-Regular';
    font-style: normal;
    font-weight: normal;
    src: local('ExpresswayRg-Regular'), url("blasanka.github.io/stackoverflow_users/static/media/expressway rg.01ac67fc.woff") format('woff');
}

h1 {
    font-family: 'ExpresswayRg-Regular';
    font-weight: 200;
    color: rgb(233, 210, 0);
}

a {
    color: black;
}

.controlls{
    display: flex;
    width: 50%;
}

@media (max-width: 500px) { 
    .controlls{
        display: block;
        width: 50%;
    }
}
